
.loot-box {
    border: 1px solid #ffffff14;

    .songs-flex {
        padding-right: 10px;
    }

    .song-selector-opt {
        border-radius: 10px !important;
        width: 45px;
        height: 45px;
        margin-top: 4px;
    }

    .clip-btn {
        margin-top: 12px;
        width: 30px;
        height: 30px;
        margin-right: 10px;
        margin-left: 10px;
    }

    //.song-option-container:hover { /*, .song-option-container.current-playing {*/
    .song-option-container:hover { //, .song-option-container.current-playing {
        .hover-over {
            display: block;
        }
    }
    .song-option-container:hover.loading-song { //, .song-option-container.current-playing {
        .hover-over {
            display: none;
        }
    }

    .song-option-container.loading-song {
        animation: bounce-load 1.5s cubic-bezier(0.5, 0.05, 0.35, 1.5) infinite;

        transform-origin: center bottom;
    }

    .song-option-container.current-playing {
        .hover-over {
            //display: block;
            //opacity: 1;
            //animation-name: disappear-hover;
            //animation-duration: 1s;
        }
    }


    .song-option-container {
        position: relative;

        .hover-over::after {
            content: "";
            position: absolute;
            left: -15px;
            right: 0px;
            margin-left: auto;
            margin-right: auto;
            bottom: -12px;
            width: 0;
            height: 0;
            border-color: transparent;
            border-top-color: #ffffffd9;
            border-style: solid;
            border-width: 12px 8px 0;
        }

        .hover-over {
            .song-name {
                color: #c1c1c1;
            }

            .artist-name {
                color: #807f7f;
            }
            border-radius: 10px;
            background-color: #4545454f;
            border: 1px solid white;
            background-color: #141313d9;
            color: #c3c3c3;
            display: none;
            position: absolute;
            //left: -35%;
            top: -90px;
            left: 50%;
            transform: translateX(-50%);
            white-space: nowrap;
            padding: 8px;
            padding-left: 10px;
            padding-right: 10px;
        }
    }

    .song-option-container.current-playing::after {
        content: "";
        width: 6px;
        height: 6px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: -12px;
        background-color: white;
        border-radius: 8px;
    }


    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    margin-left: auto;
    margin-right: auto;
    z-index: 100000000000000;
    //background-color: red;
    display: table;
    padding: 20px;
    //backdrop-filter: blur(8px);
    //background-color: #0000003b;
    padding: 10px;
    padding-bottom: 15px;
    border-radius: 32px;
    background-color: #4545454f;
    bottom: 15px;

    .zorb-selector {
        margin-left: auto;
        padding: 1px;
        padding-left: 8px;
        border-left: 1px solid #4c4c4c;
        width: 80px;
        height: 50px;
    }



    svg {
        width: 300px !important;
    }

    .loot-inner {
        width: 100%;
        //        max-width: 300px;
        //       flex-wrap: wrap;
        display: flex;
        
        
        .loot-option.user-zorb {
            position: relative;
            //border-radius: 20px;
            //border: 1px dashed #e1ffc299;
            img.checks-icon {
                position: absolute;
                width: 10px;
                height: 10px;
                right: -2px;
                bottom: -2px;
                
            }
        }

        .loot-xoption.user-zorb::after {
            content: '';
            width: 5px;
            height: 5px;
            background-color: white;
            border-radius: 5px;
            position: absolute;
            bottom: -1px;
            right: 0px;
            left: 0px;
            margin-left: auto;
            margin-right: auto;
                     
        }

        .loot-option:hover {
            border-color: white;
            animation-duration: 0.5s;
            animation-name: brighten;
        }
        .loot-option {
            border-radius: 25px;
            border: 1px dashed transparent;
            cursor: pointer;
            border: 1px solid transparent;
            transform: translate(0px, -8px);
            margin: 3px;
            img {
                width: 25px;
                height: 25px;
            }
        }
    }
}

.bigger.loot-box {
    .loot-inner {
        .loot-option {
            img {
                width: 30px;
                height: 30px;
            }
        }
    }
}

@keyframes bounce-load {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-15px);
    }
}

@keyframes bounce-load-mobile {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-8px);
    }
}

@keyframes disappear-hover {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}


@media screen and (max-width: 600px) {
    .loot-box {
        max-width: 98vw;
        display: flex;
        padding: 0px;
        padding-left: 8px;
        padding-right: 8px;
        .song-selector-opt {
            margin-top: 15px;
            margin-bottom: 15px;
            width: 40px;
            height: 40px;
            min-width: 40px;
            min-height: 40px;
        }
        .song-option-container.current-playing::after {
            bottom: 4px;;
        }

        .songs-flex::-iebkit-scrollbar {
            display: none;
        }
        
        .songs-flex {
            -ms-overflow-style: none;  /* Internet Explorer and Edge */
            scrollbar-width: none;  /* Firefox */
        }



        .songs-flex {
            max-width: 90vw;
            overflow-x: scroll;
            overflow-y: visible;
            height: 100%;
        }
        .clip-btn {
            margin-top: 20px;
        }
        .zorb-selector {
            margin-top: 10px;
            margin-bottom: 10px;
        }

        .song-option-container.loading-song {
            animation: bounce-load-mobile 1.5s cubic-bezier(0.5, 0.05, 0.35, 1.5) infinite;
            
            transform-origin: center bottom;
        }
    }

}

.main-play-container {
    position: absolute;
    left: 0;
    top: 10%;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 10000;
    width: 70px;
    height: 70px;
    border-radius: 50px;
    display: flex;
    background-color: #0000008a;
    backdrop-filter: blur(8px);
    
    .main-play-button {
        width: 20px;
        height: 20px;
        margin: auto;
    }
}

@keyframes brighten{
    0% {
        border-color: transparent;
    }

    100% {
        border-color: white;
    }
}
