
.membrane-editor {
    svg {
        .line-option.selected {
            .web-line {
                stroke-width: 2px;
            }
        }
        .line-option:hover, .line-option.selected {
            .web-line {
                stroke: white;
            }
        }

        .circle-option.selected {
            .circle-piece {
                stroke-width: 2px;
                fill: "white";
            }
        }
        .circle-option:hover, .circle-option.selected {
            .circle-piece {
                stroke: white;
                stroke-width: 4px;
                fill: "white" !important;
            }
        }
    }

    .z-number:active {
        .arrow-right {
            border-left-color: red;
        }
    }
    .z-number {
        display: flex;

        .arrow-right {
            margin-top: 5px;
            margin-right: 5px;
        }
    }
}
